.showing {
	width: 100%;
	max-width: 100%;
	background: grey url("../img/mob-phone.jpg") no-repeat top;
	background-size: cover;
	min-height: 500px;
}

.showing__wrapper {
	max-width: 1200px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 40px 10px 0;
}

.title {
	color: rgba(255, 255, 255, 1);
	margin: 0;
	text-align: center;
	width: 90%;
	font-size: 40px;
    line-height: 40px;
    font-weight: inherit;
    margin-bottom: 21px;
}

.showing__text {
	color: rgba(255, 255, 255, 1);
	text-align: center;
	width: 95%;
	margin-bottom: 32px;
}

.showing__btn {
	width: 100px;
	height: 34px;
	border-radius: 90px;
	border: none;
	cursor: pointer;
	background-color: rgba(244, 224, 65, 1);
}

@media (min-width: @tab-width) {
	.showing {
		background: grey url("../img/tab-phone.jpg") no-repeat top;
		background-size: cover;
	}

	.showing__wrapper {
		padding: 90px 10px 0;
	}

	.title {
		width: 60%;
		margin-bottom: 20px;
	}

	.showing__text {
		width: 52%;
	}
}

@media (min-width: @note-width) {
	.showing {
		background: grey url("../img/note-phone.jpg") no-repeat top;
		background-size: cover;
		min-height: 650px;
	}

	.title {
		width: 45%;
		margin-bottom: 21px;
	}

	.showing__wrapper {
		padding-top: 164px;
	}

	.showing__text {
		width: 39%;
	}
}

@media (min-width: @desc-width) {
	.showing {
		background: grey url("../img/desk-phone.jpg") no-repeat top;
		background-size: cover;
	}

	.title {
		width: 40%;
		margin-bottom: 21px;
	}

	.showing__text {
		width: 33%;
	}
}