.page-header {
	width: 100%;
	max-width: 100%;
	min-height: 60px;
	background-color: rgba(255, 255, 255, 1);
}

.page-header__wrapper {
	max-width: 1350px;
	margin: 0 auto;
	display: flex;
	justify-content: space-around;
	padding: 13px 8px 0;
}

.page-header__users,
.page-header__button {
	width: 100px;
	height: 34px;
	border-radius: 90px;
	border: none;
	cursor: pointer;
	background-color: rgba(244, 224, 65, 1);
}

.page-header__users {
	margin-right: 6px;
}

.page-header__logo {
	padding-top: 4px;
}

@media (min-width: @tab-width) {
	.page-header__wrapper {
		justify-content: space-between;
		padding: 13px 31px 0;
	}
}

@media (min-width: @note-width) {
	.page-header__wrapper {
		padding: 13px 60px 0;
	}
}
