* {
	margin: 0;
	padding: 0;
}

.page {
	height: 100%;
}

.page-body {
	font-family: "Nunito", "Arial", "sans-serif";
	font-weight: 400;
	background-color: rgba(248, 248, 248, 1);
	color: rgba(0, 0, 0, 0.87);
	font-size: 16px;
	line-height: 26px;
	margin: 0;
	padding: 0;
	overflow-x: hidden;
	min-height: 100%;
}

a {
	text-decoration: none;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

img {
	max-width: 100%;
	height: auto;
}

.hidden {
	position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

@tab-width: 768px;
@note-width: 1024px;
@desc-width: 1170px;