.form {
	width: 100%;
	max-width: 100%;
}

.form__wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 140px 16px 90px;
}

.form__group {
	border: none;
	margin: 0;
	padding: 0;
	width: 100%;
}

.form__group {
	margin-bottom: 25px;
}

.form__group:nth-child(3) {
	margin-bottom: 62px;
	display: flex;
	justify-content: center;
}

.form__group:nth-child(4) {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.form__group-radio {
	padding-right: 155px;
}

.form__title {
	font-weight: 400;
    font-size: 40px;
    line-height: 40px;
    text-align: center;
    width: 90%;
    margin-bottom: 50px;
}

.form__field[placeholder] {
	color: rgba(126, 126, 126, 1);
	font-size: 16px;
    line-height: 26px;
}

.form__field {
	display: block;
	width: 100%;
	
	max-width: 380px;
	box-sizing: border-box;
	padding: 13px 16px;
	border: 1px solid rgba(208, 207, 207, 1);
	border-radius: 4px;
	margin-bottom: 50px;
}

.form__field:last-child {
	margin-bottom: 4px;
}

.form__wrap-field {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.form__number {
	font-size: 12px;
    line-height: 14px;
    color: rgba(126, 126, 126, 1);
}

.form__wrap-number {
	display: flex;
	justify-content: center;
	padding-right: 160px;
}

.form__desc {
	font-size: 16px;
    line-height: 26px;
}

.form__radio {
	display: none;
}

.form__label {
	position: relative;
}

.form__label::before {
	content: "";
	position: relative;
	width: 20px;
	height: 20px;
	border: 1px solid #00BDD3;
	border-radius: 50%;
	display: inline-block;
	vertical-align: middle;
	margin-right: 12px;
	box-sizing: border-box;
}

.form__radio:checked + .form__label::after {
	content: "";
	position: absolute;
	display: block;
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background-color: rgba(0, 189, 211, 1);
	top: 7px;
	left: 5px;
}

.form__chooce-text {
	margin-bottom: 10px;
}

.form__item {
	margin-bottom: 7px;
}

.form__item:last-child {
	margin-bottom: 0;
}

.form__upload {
	border: none;
	margin-bottom: 65px;
}

.form__main-btn {
	color: rgba(255, 255, 255, 0.87);
	background-color: rgba(180, 180, 180, 1);
	border: none;
	width: 100px;
	min-height: 34px;
    border-radius: 80px;
}

@media (min-width: @tab-width) {
	.form__wrap-number {
	padding-right: 210px;
    }

    .form__group-radio {
    	padding-right: 206px;
    }
}

