.request {
	width: 100%;
	max-width: 100%;
}

.request__wrapper {
	padding: 140px 10px 0;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.request__subtitle {
	font-weight: 400;
    font-size: 40px;
    line-height: 40px;
    text-align: center;
    width: 80%;
    margin-bottom: 50px;
    font-weight: 400;
}

.request__item {
	display: flex;
	align-items: center;
	flex-direction: column;
	max-width: 328px;
	border-radius: 10px;
	background-color: rgba(255, 255, 255, 1);
	padding: 20px 21px;
	margin-bottom: 20px;
}

.request__link {
	color: inherit;
}

.request__text {
	text-align: center;
}

.image {
	margin-bottom: 20px;
}

.request__par {
	margin-bottom: 20px;
}

.request__button {
	width: 120px;
	height: 34px;
	border-radius: 90px;
	border: none;
	cursor: pointer;
	background-color: rgba(244, 224, 65, 1);
}

.request__list {
	margin-bottom: 30px;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.text-note,
.link-note,
.elem-desc,
.element-desc {
	display: none;
}

@media (min-width: @tab-width) {
	.request__list {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 1em;
		margin-bottom: 50px;
	}

	.request__item  {
		margin-bottom: 0;
		max-width: 344px;
	}
}

@media (min-width: @note-width) {
	.request__list {
		grid-template-columns: 1fr 1fr 1fr;
		gap: 1.8em;
	}

	.request__wrapper {
	    padding: 140px 60px 0;
	}

	.text-note,
    .link-note {
	    display: block;
	    color: inherit;
    }

    .text-false,
    .link-false,
    .element-note {
    	display: none;
    }

    
}

@media (min-width: @desc-width) {
	.text-note {
	    display: none;
    }

    .text-false {
    	display: block;
    }

    .request__wrapper {
	    padding: 140px 0 0;
	}

	.request__item  {
		
	}

	.elem-desc,
	.element-note,
	.element-desc {
		display: initial;
	}
}